import React,{useEffect} from "react"
import {
  withStreamlitConnection,
  Streamlit,
} from "streamlit-component-lib"

import ReactWeather, { useOpenWeather } from "react-open-weather"

const WeatherWidget = (props) => {

  const {locationLabel, showForecast, openWeatherObject} = props.args
  const { data, isLoading, errorMessage } = useOpenWeather(openWeatherObject)
  // const { data, isLoading, errorMessage } = useOpenWeather({
  //   key: "9077e89fc1c5303425440198aa0b736a",
  //   lat: "48.137154",
  //   lon: "11.576124",
  //   lang: "en",
  //   unit: "metric", // values are (metric, standard, imperial)
  // })
  // const locationLabel = "Munich"

  useEffect(() => {
    Streamlit.setFrameHeight()
  })

  return (
    <>
    {console.log(props.args)}
    <ReactWeather
      isLoading={isLoading}
      errorMessage={errorMessage}
      data={data}
      lang="en"
      locationLabel={locationLabel}
      unitsLabels={{ temperature: "C", windSpeed: "Km/h" }}
      showForecast={showForecast}
    />
    </>
  )
}

export default withStreamlitConnection(WeatherWidget)
